* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.hero--section {
  width: 100%;
  height: 90vh;
  background: rgba(0, 0, 0, 0.5) url(../public/images01.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-blend-mode: darken;
}
.nav--section{
  background-color: #927272;
  position: sticky;
  top: 0;
  z-index: 1;
}
.nav {
  width: 100%;
  height: 4rem;
  color: white;
  font-size: 2rem;
  display: flex;
  align-items: center;
}
.nav--site--name>a{
    text-decoration: none;
    color: #fff;
}
.nav--main{
    display: flex;
    justify-content:space-between;
    align-items: center;  
    box-shadow: 0px 3px 5px rgb(77, 62, 62);
    background-color: rgba(0, 0, 0, 0.6);
    padding: 0px 2rem;
}
.checkbtn{
    font-size: 24px;
    cursor: pointer;
    display: none;
}
#check{
    display: none;
}
.navbar--nav{
    display: flex;
    justify-content: flex-end;
    width: 100%;
}
.navlink{ 
    list-style: none;
    text-decoration: none;
    margin-left: 1rem;
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 14px;
}
.navlink:hover{
    color: #927272;
}
.navlink:active{
    color: #5c4545;
}

.hero--body {
  height: 75vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}

.hero--head {
  font-size: 2.9rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.welcome {
  letter-spacing: 3px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
}
.hero-name {
  font-size: 3rem;
  color: rgb(241, 124, 40);
}
.hero--txt {
  width: 100%;
  text-align: center;
  padding: 0px 5px 0px 5px;
  margin-top: 0.5rem;
  font-size: 17.5px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.cta {
  margin-top: 3rem;
}
.cta--btn{
    width: 8rem;
    height: 2.3rem;
    border-radius: 10px;
    background-color: rgba(82, 85, 75, 0.85);
    box-shadow: 0px 0px 7px rgb(129, 129, 129);
    color: white;
    font-size: 14px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.order{
    margin-right: 1rem;
}
.id--links{
    /* display: hidden; */
    margin-bottom: 5rem;
}

/* -----------------ABOUT COMPONENT---------- */


.about--section{
    
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about--head{
    font-size: 1.8rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    border-bottom: 2px solid black;
    margin-bottom: 2rem;
}
.about--img--sec{
    padding: 0px 2rem 0px 2rem;
}
.about--img--div{
    width: 100%;
    height: 280px;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0px 0px 7px gray;
}
.about--img{
    width: 100%;
    height: 100%;
}

.about--txt--sec{
    margin-top: 1.5rem;
    padding: 0px 2rem 0px 2rem;
}
.about--txt--head{
    text-align: center;
    font-size: 1.2rem;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
}
.about--txt--body{
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-top: 0.3rem;
    /* margin-bottom: 2.5rem; */
}


/* -----------------MENU COMPONENT-------------- */


.menu--section{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* margin-bottom: 3rem; */
}
.menu--head{
    font-size: 1.8rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    border-bottom: 2px solid black;
    margin-bottom: 2rem;
    letter-spacing: 1px;
}
.menu--body{
    display: flex;
    width: auto;
    height: auto;
    grid-gap: 20px;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 0px 2rem;

}
.menu--card{
display: flex;
flex-direction: column;
align-items: center;
border-radius: 5px;
justify-content: center;

}
.menu--img--div{
    width: 250px;
    height: 180px;
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0px 0px 3px gray;
}
.menu--foodImg{
    width: 100%;
    height: 100%;
}
.menu--txt{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 0.5rem;
}
.food--name{
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
}
.food--txt{
    font-size: 12px;
}
.food--price{
    font-weight: bold;
}


/*---------------RESERVATION PAGE--------------*/


.reservation--section{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
}
.reservation--form{
    width: 100%;
    background: rgba(199, 198, 198, 0.3) url(../public/burgerBg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-blend-mode: darken;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 0px 4rem 0px;
}
.form--row{
    width: 20rem;
    display: flex;
    justify-content: space-between;
     margin-top: 0.6rem;
}
.single{
    justify-content: flex-start;
    flex-direction: column;
}
textarea{
    resize: none;
}
.time--input{
    width: 100px;
    padding-left: 5px;
}
/* select{
    appearance: none;
} */
select, textarea, input{
    border: none;
    padding: 5px;
}
select, input{
    height: 1.8rem;
}
input{
    width: 150px;
}
.submit{
    color: white;
    background-color: orangered;
    border: none;
    height: 2rem;
}


/*---------------RESERVATION PAGE--------------*/


.footer--section{
    width: 100%;
    background-color: #000;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.footer--body{
    background-color: rgb(10, 10, 5);
    width: 100%;
    padding: 3rem;
}
.footer--sec1{
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.footer--site--name{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.address-info{
    font-size: 17px;
    margin-top: 0.5rem;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.phone--contact{
    margin-top: 0.5rem;
    font-size: 17px;
}
.phone--num{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
}
.phone--digits{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: lighter;
}
.email--contact{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
}
.email--add{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: lighter;
}
.media--info{
    margin-top: 0.8rem;
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.media--icon--div{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    width: 30px;
    height: 30px;
    background-color: rgb(48, 48, 23);
}
.footer--link--div{
    margin-top: 1.2rem;
}
.footer--link{
    text-decoration: none;
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
}
.footer--link--bullet{
    color: chocolate;
    font-weight: bold;
}
.footer--sec2{
    margin-top: 1rem;
}
.footer--input--div{
    margin-top: 1rem;
}
.footer--input{
    width: 15.4rem;
    border: none;
}
.contactUs-btn{
    width: 8rem;
    height: 2rem;
    background-color: rgb(68, 68, 28);
    color: #fff;
}
.submit, .contactUs-btn{
    cursor: pointer;
}
.footer-copyright{
    font-size: 13px;
    width: 100%;
    height: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.designer--name{
    color: chocolate;;
}


@media screen and (max-width: 750px) {
    .checkbtn{
        color: #fff;
        display: block;
        position: absolute;
        top: 20px;
        right: 25px;
        z-index: 10;
    }
    .nav--main{
        display: block;
    }
    .navbar--nav{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        z-index: 1;
        margin:0;
        top: -100rem;
        left: 0;
        transition: all 400ms ease;
        width: 100%;
        height: 100vh;
        background:  #3b2e2e;
    }
    #check:checked ~ ul{
        top:0px;
    }
    .navlink{
        margin-top: 50px;
    }
    .footer--body{
        padding: 2rem;
    }
    .phone--contact{
        margin-top: 1rem;
        font-size: 14px;
    }
    .address-info{
    font-size: 14px;
    margin-top: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    }
    .phone--num{
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    }
    .phone--digits{
        font-family: Arial, Helvetica, sans-serif;
    }
    .email--contact{
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    }
    .email--add{
        font-family: Arial, Helvetica, sans-serif;
    }
    .footer--site--links{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .footer--link{
        font-size: 14px;
    }
}

@media screen and (min-width: 750px) {
    .hero--head{
        font-size: 3.5rem;
    }
    

    .about--body{
        display: flex;
        flex-direction: row;
        width: 100%;
        justify-content: center;
        align-items: center;
    }
    .about--img--sec{
        width: 450px;
    }
    .about--txt--sec{
        width: 450px;
    }

    .footer--body{
        display: flex;
        flex-direction: row;
    }
    .footer--sec1{
        display: flex;
        width: 50%;
        justify-content: space-between;
    }
    .footer--sec2{
        width: 50%;
        margin-top: 0rem;
        float: right;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;
    }
    .address-info{
        margin-top: 1rem;
    }
    .phone--contact{
        margin-top: 1rem;
    }
    .media--info{
        margin-top: 1rem;
    }
}